// import { NEWS } from "@/app/data/data";
import { NEWS } from "@/app/data/data";
import Image from "next/image";
import Link from "next/link";
import React from "react";
import { FaFacebookF, FaLinkedinIn, FaXTwitter } from "react-icons/fa6";
import { MdOutlineLink } from "react-icons/md";

// دالة لإنشاء الـ slug
function generateSlug(title: string) {
  return title
    .toLowerCase()
    .replace(/[^a-z0-9]+/g, "-")
    .replace(/^-+|-+$/g, "");
}

type Params = {
  params: {
    slug: string;
  };
};

export default function NewsDetailPage({ params }: Params) {
  const { slug } = params;

  const newsItem = NEWS.find(
    (n) =>
      n.title
        .toLowerCase()
        .replace(/[^a-z0-9]+/g, "-")
        .replace(/^-+|-+$/g, "") === slug
  );

  if (!newsItem) {
    return <p>News not found</p>;
  }

  const relatedArticles = NEWS.filter((n) => n.id !== newsItem.id);

  // دالة لنسخ الرابط
  const handleCopyLink = () => {
    navigator.clipboard.writeText(window.location.href);
    alert("Link copied to clipboard!");
  };

  return (
    <main className="w-full bg-white text-[#1e1e1e]">
      {/* Page container: responsive padding; keeps your 70/173 on desktop */}
      <section className="mx-auto w-full px-4 sm:px-6 md:px-8 lg:px-[70px] py-32 md:py-32 lg:py-[173px]">
        {/* الصورة الرئيسية: تم وضعها هنا لتكون أول عنصر في محتوى الصفحة */}
        <div className="w-full mb-8">
          <Image
            src={newsItem.image}
            alt={newsItem.title}
            width={1200}
            height={675}
            className="w-full h-auto object-cover"
            priority
          />
        </div>

        {/* معلومات الخبر (التاريخ والعنوان) */}
        <div className="lg:max-w-4xl mx-auto">
          <div className="text-[12px] text-black/60 mb-3">{newsItem.date}</div>
          <h1 className="font-editors font-medium text-[22px] sm:text-[26px] lg:text-[34px] leading-tight mb-4">
            {newsItem.title}
          </h1>
          {newsItem.subtitle && (
            <h2 className="font-editors italic text-[18px] sm:text-[22px] leading-snug mb-6">
              {newsItem.subtitle}
            </h2>
          )}
        </div>

        {/* محتوى المقال الكامل */}
        <div className="lg:max-w-4xl mx-auto space-y-4 font-helvetica text-[14px] lg:text-[16px] leading-relaxed text-black/80">
          {newsItem.body.map((paragraph, index) => (
            <p key={index}>{paragraph}</p>
          ))}
        </div>

        {/* قسم المشاركة */}
        <div className="lg:max-w-4xl mx-auto mt-12 flex items-center space-x-4">
          <span className="text-[12px] text-black/60 font-helvetica italic">
            Share Article
          </span>
          <div className="flex items-center space-x-3">
            <a
              href={`https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(
                window.location.href
              )}`}
              target="_blank"
              rel="noopener noreferrer"
            >
              <FaFacebookF className="text-black text-xl hover:text-black/70 transition-colors" />
            </a>
            <a
              href={`https://www.linkedin.com/shareArticle?mini=true&url=${encodeURIComponent(
                window.location.href
              )}&title=${encodeURIComponent(newsItem.title)}`}
              target="_blank"
              rel="noopener noreferrer"
            >
              <FaLinkedinIn className="text-black text-xl hover:text-black/70 transition-colors" />
            </a>
            <a
              href={`https://twitter.com/intent/tweet?url=${encodeURIComponent(
                window.location.href
              )}&text=${encodeURIComponent(newsItem.title)}`}
              target="_blank"
              rel="noopener noreferrer"
            >
              <FaXTwitter className="text-black text-xl hover:text-black/70 transition-colors" />
            </a>
            <button onClick={handleCopyLink} aria-label="Copy link">
              <MdOutlineLink className="text-black text-2xl hover:text-black/70 transition-colors" />
            </button>
          </div>
        </div>

        {/* قسم المقالات ذات الصلة */}
        <section className="mt-20">
          <header className="mb-8 flex items-center justify-between">
            <h2 className="font-editors text-2xl sm:text-3xl lg:text-4xl">
              Related Articles
            </h2>
          </header>

          <div className="space-y-12 lg:space-y-14">
            {relatedArticles.slice(0, 2).map((n) => (
              <article
                key={n.id}
                className="grid grid-cols-1 md:grid-cols-2 gap-6 md:gap-8 items-center"
              >
                {/* النص على اليسار */}
                <div className="w-full order-2 md:order-1">
                  <div className="text-[12px] text-black/60 mb-3">{n.date}</div>
                  <h3 className="font-editors text-[20px] sm:text-[22px] leading-snug">
                    {n.title}
                  </h3>
                  {n.subtitle && (
                    <h4 className="font-editors italic text-[18px] sm:text-[22px] leading-snug">
                      {n.subtitle}
                    </h4>
                  )}
                  <p className="font-helvetica text-[14px] leading-relaxed text-black/80 mt-4">
                    {n.excerpt}
                  </p>
                  <div className="mt-6">
                    <Link
                      href={`/news/${generateSlug(n.title)}`}
                      className="h-[37px] mx-auto bg-[#e4dedd] text-black w-[145px] backdrop-blur-md flex items-center justify-center rounded-full font-helvetica text-[14px] hover:bg-[#d9cfcf] transition-colors"
                    >
                      Read More
                    </Link>
                  </div>
                </div>

                {/* الصورة على اليمين */}
                <div className="w-full h-auto order-1 md:order-2">
                  <Image
                    src={n.image}
                    alt={n.title}
                    width={500}
                    height={300}
                    className="w-full h-auto object-cover"
                  />
                </div>
              </article>
            ))}
          </div>
        </section>
      </section>
    </main>
  );
}
